import { initializeEditPage, genterateLastEdited } from './views'
import { updateNote,removeNote } from './notes'

const titleElement = document.querySelector('#note-title')
const bodyElement = document.querySelector('#note-body')
const remove = document.querySelector('#remove-note')
const dateElement = document.querySelector('#last-edited')
let noteId = location.hash.substring(1)

initializeEditPage(noteId)


titleElement.addEventListener('input', (event) => {
    const note = updateNote(noteId, {
        title: event.target.value
    })
    dateElement.textContent = genterateLastEdited(note.updatedAt)
})

bodyElement.addEventListener('input',(event) => {
    const note = updateNote(noteId, {
        body: event.target.value
    })
    dateElement.textContent = genterateLastEdited(note.updatedAt)
})

remove.addEventListener('click', (event) => {
    removeNote(noteId)
    location.assign('/index.html')
})

window.addEventListener('storage', (event) => {
    if (event.key === 'notes'){
        initializeEditPage(noteId)
    }
})
